.wp-block-udemy-plus-header-tools {
	margin-top: 0px !important;
	display: flex;
}
.wp-block-udemy-plus-header-tools a {
	-webkit-text-decoration-line: none;
	text-decoration-line: none;
}
.wp-block-udemy-plus-header-tools button.mobile-collapse-btn {
	display: block;
	padding: 0.75rem;
	font-size: 1.5rem;
	line-height: 2rem;
}
@media (min-width: 768px) {
	.wp-block-udemy-plus-header-tools button.mobile-collapse-btn {
		display: none;
	}
}
.wp-block-udemy-plus-header-tools .signin-link {
	margin-right: 0.5rem;
	display: flex;
	align-items: center;
}
.wp-block-udemy-plus-header-tools .signin-icon {
	position: relative;
	height: 2.75rem;
	width: 2.75rem;
	text-align: center;
	line-height: 2.5rem;
}
.wp-block-udemy-plus-header-tools .signin-icon .bi {
	font-size: 1.5rem;
	line-height: 2.5rem;
}
.wp-block-udemy-plus-header-tools .signin-text {
	display: none;
	white-space: nowrap;
	padding-left: 0.25rem;
	font-size: 0.875rem;
	line-height: 1.25rem;
}
@media (min-width: 1024px) {
	.wp-block-udemy-plus-header-tools .signin-text {
		display: block;
	}
}
.wp-block-udemy-plus-header-tools .signin-text small {
	display: block;
	--tw-text-opacity: 1;
	color: rgb(107 114 128 / var(--tw-text-opacity));
}
